// logo
.details-logo {
  margin: 0 auto;
  width: 1.46rem;
  height: .4rem;
  background: {
    image: url(../img/tencent_logo.png);
    size: 100%;
    position: center;
    repeat: no-repeat;
  };
}

.details-1 {
  // 对话框
  .details-dialogue {
    position: relative;
    margin-bottom: 0.36rem;

    &-content {
      position: relative;
      z-index: 1;
      margin: 0 auto;
      width: 6.5rem;
      height: 1.8rem;
      margin-top: 2rem;
      background: {
        image: url(../img/detail_dialogue.png);
        size: 100%;
        position: center;
        repeat: no-repeat;
      };
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;

      font-size: 0.24rem;
      line-height: 0.32rem;
      color: #0a1571;
      white-space: pre-line;
    }

    // 机器人
    &-robote {
      position: absolute;
      left: 50%;
      top: -2.06rem;
      margin-left: -1.54rem;
      width: 3.09rem;
      height: 2.34rem;
      background: {
        image: url(../img/detail_robot.png);
        size: 100%;
        position: center;
        repeat: no-repeat;
      };
    }

    // 飞碟
    &-ufo {
      position: absolute;
      z-index: 1;
      //left: 50%;
      right: 0.12rem;
      top: -0.36rem;
      width: 2.24rem;
      height: 1.31rem;
      background: {
        image: url(../img/detail_ufo.png);
        size: 100%;
        position: center;
        repeat: no-repeat;
      };
    }
  }

  // 提问
  .details-1-q {
    margin: 0 auto;
    margin-bottom: 1.2rem;
    width: 4.84rem;
    height: 1.05rem;
    background: {
      image: url(../img/detail_1_q.png);
      size: 100%;
      repeat: no-repeat;
      position: center;
    };
  }

  // 按钮
  .details-1-btns {
    position: relative;

    .details-1-btns-item {
      display: block;
      margin: 0 auto;
      text-decoration: none;
      color: #f7f9ff;
      font-size: .36rem;
      width: 4.67rem;
      height: .9rem;
      line-height: .8rem;
      text-align: center;
      -webkit-tap-highlight-color: rgba(0,0,0,0);

      background: {
        image: url(../img/detail_btn.png);
        size: 100%;
        position: center;
        repeat: no-repeat;
      };
    }

    .details-btns-1 {
      margin-bottom: 0.3rem;
    }


  }
}


